import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            DiseaseName: [],
            search: '',
            DiseaseList: [],
            page:1
        }
    },
    watch: {
        search(){
            this.searchFor()
        }
    },
    created(){
        this.searchFor()
    },
    mounted(){
        this.DiseaseName = this.$store.state.Disease
    },
    methods: {
        searchFor(){
            if(this.search){
                this.$post('PlatFormAPI/KnowledgeBase/GetDepDiseaseForPage',{
                    DepartmentId: -1,
                    DiseaseName: this.search,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.DiseaseList = o.ReturnData
                })
            }
        },
        pop(idx){
            this.DiseaseName.splice(idx,1)
        },
        confirm(){
            this.$store.dispatch('pushDisease', this.DiseaseName)
            this.$router.back()
        },
        choose(i){
            const idx = this.DiseaseName.indexOf(i.DiseaseName)
            if(idx == -1){
                this.DiseaseName.push(i.DiseaseName)
            }
            else{
                this.DiseaseName.splice(idx, 1)
            }
        }
    }
}



